<template>
   <div class="inputBox shadow">
      <input type="text" v-model="newTodoItem" v-on:keyup.enter="addTodo">
      <span class="addContainer" v-on:click="addTodo">
         <i class="far fa-plus-square addBtn"></i>
      </span>
   </div>
</template>

<script>
export default{
   name: 'MainView',
   data:function(){
      return{
         newTodoItem:""
      }
   },
   methods: {
   addTodo: function(){
      console.log(this.newTodoItem);   //this는 현재 컴포넌트(mainview,vue),newTodoItem는 속성(그릇)
      //저장하는 로직
      localStorage.setItem(this.newTodoItem, this.newTodoItem);
      this.newTodoItem=""; //비워주기(초기화), = 대입연산자(넣어라)
      },
   clearInput: function() {
      this.newTodoItem=""; //비워주기(초기화)
      }
   }
}
</script>

